import Page from "src/Page";
import { Button, Flex, Text, Box, BoxProps, Input } from "@pancakeswap/uikit";
import { AutoFlex } from "src/components/AutoFlex/AutoFlex";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ActionInput, InputPanel } from "src/components/InputWrap";
import { useState } from "react";

const CardBoxStyle = styled(Box)`
  margin: 20px 0;
  background: linear-gradient(to left, #abbfff, #8afff9);
  border-radius: 20px;
  width: 47%;
  padding: 50px 0;
  @media (max-width: 1200px) {
    width: 100%;
  }
`;
const CardBox: React.FC<
  { topText: string; bottomText: string } & BoxProps &
    React.HTMLAttributes<HTMLDivElement>
> = ({ topText, bottomText, ...porps }) => {
  return (
    <CardBoxStyle {...porps}>
      <Text color="#0034b4" bold fontSize="24px">
        {topText}
      </Text>
      <Text color="#295358" bold fontSize="24px">
        {bottomText}
      </Text>
    </CardBoxStyle>
  );
};

export default function Medal() {
  const { t } = useTranslation();
  const [inputValue, setInputValue] = useState("");
  return (
    <Page>
      <Box width="70%" mt="40px">
        <AutoFlex isWrap justifyContent="space-between">
          <CardBox
            topText={t("Number of lottery tickets held in the whole network")}
            bottomText={"1999"}
          />
          <CardBox topText={t("My lottery ticket Holds")} bottomText={"0"} />
          <CardBox
            topText={t("Number of lottery draws on the whole network")}
            bottomText={"4,051"}
          />
          <CardBox topText={t("Number of individual wins")} bottomText={"12"} />
          <CardBox
            topText={t("The total amount of winnings in the whole network")}
            bottomText={"12"}
          />
          <CardBox
            topText={t("Total amount of individual winnings")}
            bottomText={"12"}
          />
        </AutoFlex>
      </Box>

      <Box width="70%" mt="40px">
        <Text mb="20px" textAlign="left" bold fontSize="30px">
          {t("Current prize pool amount")} {0} {t("Lucky Angel")}
        </Text>
        <Text textAlign="left" bold fontSize="30px" color="#000">
          {t("My lottery ticket")}:
          <span style={{ fontSize: "16px" }}>
            {"NFT Contract Address:0x9f278be3867CA5e6DDb99ED85C3F035CbA1A02D5"}
          </span>
        </Text>
      </Box>
      <Flex width="70%" mt="40px">
        <Text mb="20px" textAlign="left" bold fontSize="30px">
          {t("Participate in the draw")}:
        </Text>
        <Flex ml="20px" flexDirection="column">
          <Input
            type="text"
            scale="sm"
            value={inputValue}
            placeholder={t("Number of pledged tokens")}
            onInput={(e) => {
              setInputValue((e.target as any).value);
            }}
          />
          <Button mt="10px">{t("pledge")}</Button>
          <Button mt="10px" variant="secondary" disabled={!inputValue}>
            {t("Fill in the lottery")}
          </Button>
        </Flex>
      </Flex>
    </Page>
  );
}
